@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.social-items {
    position: absolute;
    padding: 0;
    display: -webkit-flex;
    display: flex;
}

.social-items li {
    list-style: none;
}

.social-items li a {
    position: relative;
    width: 60px;
    height: 60px;
    display: block;
    text-align: center;
    margin: 0 10px;
    border-radius: 50%;
    padding: 6px;
    box-sizing: border-box;
    text-decoration: none;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
    background: linear-gradient(0deg, #ddd, #fff);
    transition: .5s;
}

.social-items li a:hover{
    box-shadow: 0 2px 5px rgba(0,0,0,0.3);
    text-decoration: none; 
}

.social-items li a:hover .fa {
    color: #3b5998;
}

.social-items li a .fa {
    width: 100%;
    height: 100%;
    display: block;
    background: linear-gradient(0deg, #fff, #ddd);
    border-radius: 50%;
    line-height: calc(60px - 12px);
    font-size: 24px;
}

