.scrollTop {
    position: fixed; 
    width: 190%;
    bottom: 20px;
    -webkit-align-items: right;
            align-items: right;
    height: 20px;
    -webkit-justify-content: right;
            justify-content: right;
    z-index: 0;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    opacity: 0.7;
  }
  
  .scrollTop:hover{
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
.counters {
	background: #0f479a;
	color: #fff;
	padding: 50px 60px;
	border-top: 3px lightskyblue solid;
	margin-left: -65px;
}

.counters .container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 30px;
	text-align: center;
}

.counters i {
	color: lightskyblue;
	margin-bottom: 5px;
}

.counters .counter {
	font-size: 45px;
	margin: 10px 0;
}

@media (max-width: 700px) {
	.counters .container {
		grid-template-columns: repeat(3, 1fr);
		margin-top: 50px;
		margin-left: 45px;
	}

	.counters .container > div:nth-of-type(1),
	.counters .container > div:nth-of-type(2),
	.counters .container > div:nth-of-type(3) {
		border-bottom: 1px lightskyblue solid;
		padding-bottom: 15px;
	}
}

@media (max-width: 480px) {
	.counters .container {
		grid-template-columns: repeat(3, 1fr);
		margin-top: 50px;
		margin-left: 10px;
	}

	.counters .container > div:nth-of-type(1),
	.counters .container > div:nth-of-type(2),
	.counters .container > div:nth-of-type(3) {
		border-bottom: 1px lightskyblue solid;
		padding-bottom: 15px;
	}
}

@media (max-width: 400px) {
	.counters .container {
		grid-template-columns: repeat(1, 1fr);
		margin-top: 50px;
	}

	.counters .container > div:nth-of-type(1),
	.counters .container > div:nth-of-type(2),
	.counters .container > div:nth-of-type(3) {
		border-bottom: 1px lightskyblue solid;
		padding-bottom: 15px;
	}
}
